.accordion {
  .accordion-body {
    display: none;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 18px;
  }
  .accordion-title {
    cursor: pointer;
    margin: 0;
    line-height: 1;
    padding-top: 14px;
    padding-bottom: 14px;
    font-size: 22px;
    padding-right: 50px;
    position: relative;
    .arrow {
      display: block;
      width: 34px;
      height: 34px;
      border-radius: 50%;
      background: @blue;
      position: absolute;
      right: 0;
      top: 8px;
      transition: all .25s;
      pointer-events: none;
      &::after {
        display: block;
        content: "";
        width: 9px;
        height: 9px;
        border-top: 2px solid white;
        border-right: 2px solid white;
        border-top-right-radius: 2px;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-top: 2px;
        transform: translate(-50%, -50%) rotate(-45deg) ;
      }
    }


    &.open {
      .arrow {
        background: #efefef;
        transform: scale(1, -1);
        &::after {
          border-color: @blue;
        }
      }
    }
  }
}

.aside-text {
  border-bottom: 2px solid @blue;
  font-size: 20px;
  @media (min-width: 768px) {
    font-size: 22px;
    border-left: 2px solid @blue;
    padding-left: 30px;
    border-bottom: none;
  }
}
