.header {

  background: white;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 6px solid @blue;
  @media (min-width: 1200px) {
    padding-top: 25px;
    padding-bottom: 25px;
    border-bottom: none;
  }



  .logo {
    > * {
      display: inline-block;
      vertical-align: middle;
      white-space: nowrap;
    }
  }

  @media (max-width: 767px) {
    .logo {
      width: 100%;
      a {
        width: calc(~"100% - 75px");
        max-width: 50%;
      }
      position: relative;

      .menu-trigger {
        position: absolute;
        top: 50%;
        right: 15px;
        transform: translateY(-50%);
      }

    }
  }

}

.header-sticky {
  top: 0;
  z-index: 1000;
}



.menu-trigger {

  margin-left: 15px;
  .icon {
    color: @blue;
    font-size: 20px;
  }

}

.menu-wrap {

  .link-colored(@black_text, none);
  a {
    text-transform: uppercase;
    font-size: 14px;
    position: relative;
    &::after {
      display: block;
      content: ".........................................................................";
      position: absolute;
      left: 0;
      right: 0;
      overflow: hidden;
      top: 50%;
      transition: opacity .25s;
    }
    &:hover {
      &:after {
        opacity: 0;
      }
    }
  }
  .active {
    a {
      font-weight: bold;
      &:after {
        display: none;
      }
    }
  }
  li {
    @media (min-width: 1200px) {
      &:not(:last-of-type) {
        margin-right: 20px;
      }
    }
  }

  @media (max-width: 991px) {




    position: fixed;
    z-index: 1000;
    opacity: 0;
    pointer-events: none;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: all .25s;
    background: @gray_bg;
    //border-top: 8px solid @blue;
    //border-top-right-radius: 3px;
    //border-bottom-right-radius: 3px;
    padding: 40px 50px !important;

    &.open {
      opacity: 1;
      pointer-events: auto;
    }

    li {
      margin-bottom: 15px;
      display: block;
    }

    a {
      font-size: 20px;
      &::after {
        top: 40%;
      }
    }

    .active {
      margin-bottom: 7px;
    }

  }
}

.get-loan {
  text-align: center;
  border-right: 1px solid @gray_bg;
  .icon {
    display: block;
    color: @blue;
    font-size: 26px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
  }
  .text {
    text-transform: uppercase;
    font-weight: bold;
    font-size: 14px;
    color: @blue;
  }
}

.phone {
  font-size: 14px;

  @media (min-width: 768px) {
    text-align: right;

  }
  .small-text {
    color: @gray_text;
    font-size: 1em;
  }
  .link-colored(@blue, none);
  a {
    font-weight: bold;
    font-size: 1.714285714285714em;
    position: relative;
    &::after {
      display: block;
      content: ".........................................................................";
      position: absolute;
      left: 0;
      right: 0;
      overflow: hidden;
      top: 30%;
      transition: opacity .25s;
      font-weight: normal;
    }
    &:hover {
      &:after {
        opacity: 0;
      }
    }
  }
}

.mobile-buttons {
  .top-row {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .phone {
    font-size: 11px;
  }
  .title {
    text-transform: uppercase;

    font-weight: bold;
    background: @red;
    color: white;
    padding: 15px 30px;
    font-size: 24px;


  }
  .subtitle {

    font-weight: bold;
    background: @dark_red;
    color: white;
    line-height: 1;

    padding: 15px 30px;
    font-size: 18px;

  }
}
