
.main-slider {

  .main-slider-dots {
    white-space: nowrap;
    bottom: 10px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    @media (min-width: 768px) {
      bottom: 20px;
      left: 15px;
      transform: none;
    }
    @media (min-width: 992px) {
      left: 30px;

    }
    @media (min-width: 1200px) {
      left: 0;

    }
    button {
      background: white;
      height: 6px;

      border-radius: 3px;
      transition: background .75s;
      width: 10px;
      @media (min-width: 768px) {
        width: 40px;
      }
      @media (min-width: 992px) {
        width: 60px;
      }
      @media (min-width: 1200px) {
        width: 68px;
      }
    }
    .slick-active {
      button {
        background: @blue;
      }
    }
  }
  .title {
    text-transform: uppercase;

    font-weight: bold;
    background: @red;
    color: white;
    display: inline-block;
    padding: 7px 23px;
    font-size: 14px;
    @media (min-width: 768px) {
      padding: 15px 30px;
      font-size: 24px;
    }
    @media (min-width: 992px) {
      padding: 15px 45px;
      font-size: 30px;
    }

  }
  .subtitle {

    font-weight: bold;
    background: @dark_red;
    color: white;
    line-height: 1;
    display: inline-block;
    padding: 7px 23px;
    font-size: 10px;
    @media (min-width: 768px) {
      padding: 15px 30px;
      font-size: 18px;
    }
    @media (min-width: 992px) {
      padding: 15px 45px;
      font-size: 22px;
    }
  }
  .slide {
    position: relative;
    img {
      display: block;
    }

    .content {
      position: absolute;
      top: 50%;
      left: 30px;
      transform: translateY(-50%);
      @media (min-width: 768px) {
        left: 0;
      }
      //@media (min-width: 1200px) {
      //  left: 0;
      //}
    }
  }

  @media (min-width: 768px) {
    position: relative;
    left: calc(~"360px - 50vw");
  }
  @media (min-width: 992px) {
    left: calc(~"470px - 50vw");
  }
  @media (min-width: 1200px) {
    left: calc(~"570px - 50vw");
    .slide {
      .content {
        padding-left: calc(~"50vw - 570px");
      }
    }
    .main-slider-dots {
      left: calc(~"50vw - 570px");
    }
  }
  @media (min-width: 1920px) {
    left: -390px;
    .slide {
      .content {
        padding-left: 390px;
      }
    }
    .main-slider-dots {
      left: 390px;
    }
  }
}
