.about-main {
  padding-top: 15px;
  padding-bottom: 10px;
  overflow: hidden;
  @media (min-width: 768px) {
    background-image: url(../img/about_map_bg.jpg);
    background-repeat: no-repeat;
    background-position: 20% 160px;
    background-size: auto 450px;
    min-height: 500px;
    padding-bottom: 50px;
    margin-bottom: -10px;
  }
  @media (min-width: 992px) {
    background-position: 30% top;
    background-size: cover;
    min-height: 815px;
    padding-bottom: 150px;
    margin-bottom: -90px;
  }
  @media (min-width: 1200px) {
    background-position: center top;
    padding-bottom: 100px;
    margin-bottom: -140px;
  }
  .main-title {
    margin-bottom: 20px;
    @media (min-width: 992px) {
      margin-bottom: 90px;
    }
  }
  .content-wrap {
    .flex-item {
      width: 100%;
    }
    @media (min-width: 768px) {
      .flex-item {
        width: 50%;
      }

      padding-right: 30px;
    }
    @media (min-width: 992px) {
      padding-left: 300px;
      padding-right: 15px;
      .flex-item {
        width: initial;
      }
    }
    @media (min-width: 1200px) {
      padding-left: 465px;
      padding-right: 0;
    }

  }
  .main-text {
    text-transform: uppercase;
    font-weight: bold;
    font-size: 18px;

    margin-top: 0;
    margin-bottom: 20px;
    @media (min-width: 768px) {
      font-size: 26px;
      margin-bottom: 10px;
    }
    @media (min-width: 992px) {
      font-size: 28px;
      margin-bottom: 20px;
    }

  }
  .number-wrap {
    white-space: nowrap;
    > * {
      white-space: normal;
    }

    display: inline-block;
    font-size: 16px;
    width: 40%;
    margin-bottom: 30px;

    @media (min-width: 992px) {
      display: block;
      font-size: 22px;
      width: initial;
      margin-bottom: 0;
    }
    .text {
      display: inline-block;
      vertical-align: top;
      font-size: 1em;
      font-weight: 300;
      width: 150px;
      line-height: 1.1;
    }
    .number {
      display: inline-block;
      vertical-align: top;
      font-size: 2.454545454545455em;
      font-weight: bold;
      margin-right: 10px;
      line-height: 1;
      @media (min-width: 992px) {
        width: 90px;
        text-align: right;
      }
    }
  }
  .about-text {
    font-size: 16px;

    p {
      margin-bottom: 1em;
    }
  }
}

.about-blue-line {
  background: fade(#d5e2f2, 50%);
  padding-top: 25px;
  overflow: hidden;
  .img-wrap {
    max-width: 180px;
    margin-right: -45px;
    margin-left: -30px;
    @media (min-width: 768px) {
      max-width: 265px;
      margin-right: 0;
    }
    @media (min-width: 992px) {
      max-width: initial;
    }
    @media (min-width: 1200px) {
      margin-right: 70px;

    }
  }
  .content-wrap {
    position: relative;

    margin-bottom: 30px;
    line-height: 1.1;
    font-size: 14px;
    @media (min-width: 768px) {
      font-size: 22px;
      padding-top: 70px;
      margin-bottom: 90px;

      &::before {
        display: block;
        content: url(../img/quotes.svg);
        position: absolute;
        width: 83px;
        height: 62px;
        transform: scale(.75);
        transform-origin: 0 0;
        top: 0;
        left: 0;

      }
    }
    @media (min-width: 992px) {
      font-size: 30px;
      padding-left: 120px;
      padding-top: 25px;
      &::before {
        transform: none;

      }
    }
    .main-text {
      text-transform: uppercase;
      font-weight: bold;
      margin-top: 0;
      margin-bottom: 10px;
    }
    .text {
      margin-bottom: 1em;
    }
    .signature {

      line-height: 1.2;
      font-size: .75em;
      @media (min-width: 992px) {
        font-size: .5em;
      }

    }
  }
}

.about-history {
  padding-top: 50px;
  padding-bottom: 50px;
  .main-title {
    margin-bottom: 30px;
    @media (min-width: 768px) {
      margin-bottom: 50px;
    }
  }
  .number-wrap {
    position: relative;
    padding-left: 100px;
    //@media (min-width: 768px) {
    //  padding-left: 100px;
    //}
    //@media (min-width: 992px) {
    //  padding-left: 100px;
    //}
    .icon {
      position: absolute;
      top: 16px;
      left: 10px;
      font-size: 50px;
      @media (min-width: 768px) {
        top: 14px;
        font-size: 60px;
        left: 5px;
      }
      @media (min-width: 992px) {
        top: 21px;
      }
      @media (min-width: 1200px) {
        top: 30px;
      }
    }
    .left-img {
      position: absolute;
      top: 0;
      left: -40px;
      max-width: 120px;
      @media (min-width: 768px) {
        left: -100px;
        max-width: initial;
      }
    }
    .content {
      @media (min-width: 768px) {
        padding-left: 25px;

      }
      @media (min-width: 992px) {
        padding-left: 35px;
      }
      &.pr {

        @media (min-width: 992px) {
          padding-right: 30px;
        }
        @media (min-width: 1200px) {
          padding-right: 80px;
        }
      }
    }
    .number {

      font-weight: 300;
      line-height: .9;
      padding-top: 25px;
      padding-bottom: 25px;
      position: relative;
      font-size: 20px;
      @media (min-width: 768px) {
        font-size: 24px;
      }
      @media (min-width: 992px) {
        font-size: 26px;
      }
      @media (min-width: 1200px) {
        font-size: 35px;
      }
      &::before {
        display: block;
        content: "";
        position: absolute;
        top: 0;
        left: -15px;
        bottom: 0;
        width: 3px;
        background: @blue;
        border-right: 2px;
        @media (min-width: 768px) {
          left: -35px;
        }

      }
      display: flex;
      align-items: flex-end;
      .big {
        font-size: 2em;
        line-height: .8;
        margin-right: 10px;
      }
    }
    p {
      font-size: 16px;
      line-height: 1.6;

    }
    &.with-back {

      @media (min-width: 768px) {
        padding-left: 140px;
        margin-left: -40px;
      }
      @media (min-width: 992px) {
        padding: 40px 40px 40px 140px;
        &::before {
          display: block;
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: white;
          box-shadow: 0 9px 59px rgba(210, 210, 210, 0.61);
          border-radius: 3px;
        }
      }
      .content {
        position: relative;
      }
      .left-img {
        top: 0;
        left: 0;
        max-width: 80px;
        @media (min-width: 768px) {
          left: 30px;
          max-width: initial;

        }
        @media (min-width: 992px) {
          top: 30px;
        }
      }
    }
  }
}

.about-team {
  padding-top: 50px;
  padding-bottom: 50px;
}

.team-item {

  .photo-wrap {
    max-width: 200px;
    margin-bottom: 15px;
  }
  .frame-wrap {
    position: relative;
    width: 100%;
    padding-bottom: 100%;
    .frame {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: auto;
    }
    .img-circle {
      position: absolute;
      top: 12px;
      left: 12px;
      width: calc(~"100% - 24px");
      height: calc(~"100% - 24px");
      filter: grayscale(100%);
      transition: filter .25s;
    }
    &:hover {
      .img-circle {
        filter: grayscale(0);
      }
    }
  }
  .content-wrap {
    font-size: 14px;
    line-height: 1.1;
    @media (min-width: 992px) {
      padding-left: 20px;
    }
    @media (min-width: 1200px) {
      padding-left: 60px;
    }
    .name {
      font-weight: bold;
      font-size: 1.285714285714286em;
    }
    .city {
      font-weight: bold;
    }
  }

}

.team-slider {

  margin-bottom: -35px;
  margin-left: 40px;
  margin-right: 40px;
  @media (min-width: 768px) {
    margin-left: 40px;
    margin-right: 40px;
  }
  @media (min-width: 992px) {
    margin-left: -15px;
    margin-right: -15px;
  }
  .slide {
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 35px;
  }

}

.about-info {
  padding-top: 50px;
  padding-bottom: 50px;
  font-size: 16px;

}


