input[type="text"],
textarea,
select,
input[type="password"] {
  box-sizing: border-box;
  width: 100%;
  outline: none;
  font-family: @base-font;
  font-size: 18px;
  line-height: 20px;
}

label {
  cursor: pointer;
}

button {
  outline: none;
  cursor: pointer;
}

input:focus {
  outline: none;
}

input::-webkit-input-placeholder {
  opacity: 0.8;
}

input::-moz-placeholder {
  opacity: 0.8;
}

select:not(.unstyled) {
  display: none;
}
.jq-number {
  display: block;
}


.form-control,
.jq-number input,
.form-control-div {
  display: block;
  width: 100%;
  padding: 7px 9px;
  height: 36px;
  border: 1px solid #b8b8b8;
  background-color: #f5f5f5;
  border-radius: 3px;
  @media (min-width: 768px) {
    padding: 15px 18px;
    height: 52px;
  }
  &:focus {
    border-color: @blue;
    //box-shadow: inset 0 1px 1px fade(black, 7.5%), 0 0 8px fade(@red, 60%);
    box-shadow: none;
  }
  &::placeholder {
    font-size: 14px;
    opacity: 1;
    color: #a7a7a7;
  }
  &::-moz-placeholder {
    font-size: 14px;
    opacity: 1;
    color: #a7a7a7;
  }
  &::-ms-input-placeholder {
    font-size: 14px;
    opacity: 1;
    color: #a7a7a7;
  }
  &::-webkit-input-placeholder {
    font-size: 14px;
    opacity: 1;
    color: #a7a7a7;
  }
  @media (min-width: 768px) {
    &::placeholder {
      text-transform: uppercase;
      font-size: 16px;
    }

    &::-moz-placeholder {
      text-transform: uppercase;
      font-size: 16px;
    }

    &::-ms-input-placeholder {
      text-transform: uppercase;
      font-size: 16px;
    }

    &::-webkit-input-placeholder {
      text-transform: uppercase;
      font-size: 16px;
    }
  }
}

textarea.form-control {
  .webkit-scrollbar();
}

.jq-selectbox__select {
  padding: 0 35px 0 13px;
  border-radius: 3px;
  font: 16px/34px @base-font;
  color: #a7a7a7;
  border: 1px solid #b8b8b8;
  //background-color: #f5f5f5;
  background: white;
  @media (min-width: 768px) {
    font: 16px/50px @base-font;
  }
}

.opened {
  .jq-selectbox__select {
    border-color: @red;
    //box-shadow: inset 0 1px 1px fade(black, 7.5%), 0 0 8px fade(@red, 60%);
    box-shadow: none;
  }
}

.jq-selectbox__select-text {
  display: block;
  width: 100% !important;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-transform: uppercase;
  font-size: 16px;
  &.placeholder {
    color: #a7a7a7;
  }
  //span {
  //  display: block;
  //  height: 50px;
  //}
}

.jq-selectbox__trigger {
  position: absolute;
  top: 0;
  right: 0;
  width: 34px;
  height: 100%;
  //border-left: 1px solid #CCC;
  font-size: 24px;
  color: #999;
}

.jq-selectbox__trigger-arrow {
  position: absolute;
  top: 50%;
  margin-top: -18px;
  right: 9px;
  display: inline-block;
  font-weight: normal;
  font-variant: normal;
  font-style: normal;
  font-family: Arial, Baskerville, monospace;
  color: @red;
  font-size: 16px;
  cursor: pointer;
  transition: transform .25s, margin .25s;
  transform: scale(1, .8);
  &::before {
    content: "▼";
  }
  @media (min-width: 768px) {
    margin-top: -25px;
  }
}

.opened {
  .jq-selectbox__trigger-arrow {
    transform: scale(1, -.8);
    margin-top: -20px;
    @media (min-width: 768px) {
      margin-top: -27px;
    }
  }
}

.jq-selectbox__dropdown {
  box-sizing: border-box;
  width: 100%;
  margin: 2px 0 0;
  padding: 0;
  border: 1px solid #CCC;
  //border-radius: 4px;
  background: #FFF;
  box-shadow: 0 2px 10px rgba(0, 0, 0, .2);
  font: 14px/18px @base-font;
  text-align: left;
  //max-height: 50vh;
  min-width: 100%;
  ul {
    max-height: 50vh;
    overflow: auto;
    .webkit-scrollbar();
  }
}

.jq-selectbox__search {
  margin: 5px;

  input {
    box-sizing: border-box;
    width: 100%;
    margin: 0;
    padding: 5px 27px 6px 8px;
    border: 1px solid #CCC;
    border-radius: 0;
    outline: none;
    background: url('data:image/png;base64,') no-repeat 100% 50%;
    box-shadow: inset 1px 1px #F1F1F1;
    color: #333;
  }
}

.jq-selectbox__not-found {
  margin: 5px;
  padding: 5px 8px 6px;
  background: #F0F0F0;
  font-size: 13px;
}

.jq-selectbox ul {
  margin: 0;
  padding: 0;
  border-radius: 0;
}

.jq-selectbox li {
  //min-height: 18px;
  padding: 7px 10px 5px!important;
  color: @black_text;
  margin-bottom: 0!important;
  //padding-left: 10px !important;
  //margin-bottom: 0 !important;
  //&::before {
  //  display: none !important;
  //}
  &::before {
    display: none!important;
  }
  &.selected {
    background-color: @gray;
    color: @black_text;
  }
  &:hover {
    background-color: @red;
    color: white;
  }
  &.disabled {
    color: #AAA;
    &:hover {
      background: none;
    }
  }
  &.optgroup {
    font-weight: bold;
    &:hover {
      background: none;
      color: @black_text;
      cursor: default;
    }
  }
  &.option {
    padding-left: 25px;
  }

}

