.jq-file {
  display: inline-block;
  vertical-align: middle;
  padding: 7px 10px;
  border: 1px solid @disable;
  background-color: @disable;
  transition: box-shadow .25s;
  line-height: 1;
  position: relative;
  @media (min-width: 768px) {
    padding: 14px 20px;
  }
  &:hover {
    text-decoration: none;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.25);
  }
  &:focus {
    text-decoration: none;
    box-shadow: 0 0 0 0 transparent;
  }
  &:active {
    box-shadow: 0 0 0 0 transparent;
    top: 1px;
  }
  input {
    cursor: pointer;
    width: 100%;
    height: 100%;
  }
  .jq-file__name {
    font-size: 16px;
    line-height: 1.4;
    display: inline-block;
    vertical-align: middle;
    text-transform: uppercase;
    color: white;
  }
  .jq-file__browse {
    position: absolute;
    top: 1px;
    left: 1px;
    font-size: 16px;
    color: @red;
  }

  &.jq-file:hover .jq-file__browse {
    color: @red;
  }
  &.jq-file:active .jq-file__browse {

    color: @red;
  }

  &.jq-file.focused .jq-file__name {

  }

  &.jq-file.disabled,
  &.jq-file.disabled .jq-file__name,
  &.jq-file.disabled .jq-file__browse {
    border-color: #CCC;
    background: #F5F5F5;
    box-shadow: none;
    color: #888;
  }
}

input[type=checkbox]:not(.unstyled),
input[type=radio]:not(.unstyled) {
  display: none;
}

.jq-checkbox,
.jq-radio {
  vertical-align: -8px;
  width: 24px;
  height: 24px;
  margin: 0 4px 0 0;
  border: 1px solid #C3C3C3;
  background: white;
  //box-shadow: 0 0 1px rgba(0, 0, 0, .05);
  cursor: pointer;
  box-sizing: content-box;
}




.jq-checkbox.focused,
.jq-radio.focused {
  border: 1px solid @blue;
}

.jq-checkbox.disabled,
.jq-radio.disabled {
  opacity: .55;
}

.jq-checkbox {
  border-radius: 3px;
  + span {
    max-width: calc(~"100% - 30px");
    vertical-align: top;
  }
}

.jq-checkbox.checked {
  //border-color: @green;
  box-shadow: inset 0 0 0 2px white;
  background: @red;
  .jq-checkbox__div {
    width: 9px;
    height: 5px;
    margin: 6px 0 0 6px;
    border-bottom: 3px solid white;
    border-left: 3px solid white;
    transform: rotate(-50deg);
    box-sizing: content-box;
  }
}

.jq-radio {
  border-radius: 50%;
}

.jq-radio.checked {
  background: @red;
  border-color: @red;
  .jq-radio__div {
    width: 12px;
    height: 12px;
    margin: 6px 0 0 6px;
    border-radius: 50%;
    background: white;
    //box-shadow: inset 1px 1px 1px rgba(0, 0, 0, .7);
  }
}

.jq-checkbox,
.jq-radio {
  vertical-align: -8px;
  width: 24px;
  height: 24px;
  margin: 0 4px 0 0;
  border: 1px solid #C3C3C3;
  background: white;
  //box-shadow: 0 0 1px rgba(0, 0, 0, .05);
  cursor: pointer;
  box-sizing: content-box;
}


.doc-type-selector  {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  flex-wrap: wrap;
  .selector-item {
    span {
      display: block;
      width: 235px;
      height: 50px;
      border: 1px solid #a7a7a7;
      background: #f5f5f5;
      position: relative;
      color: #a7a7a7;
      text-transform: uppercase;
      line-height: 50px;
      font-size: 16px;
      font-weight: normal;
      padding-left: 50px;
      &::before {
        display: block;
        content: "";
        width: 18px;
        height: 18px;
        background: #a7a7a7;
        position: absolute;
        top: 15px;
        left: 15px;
      }
    }

    &:not(:first-child) {
      span {
        margin-left: -1px;
      }

    }


    input:checked + span {
      border-color: @red;
      z-index: 1;
      color: @black_text;
      &::before {
        background: @red;
      }
    }
  }
}



