.btn {
  display: inline-block;
  box-sizing: border-box;
  cursor: pointer;
  text-align: center;
  vertical-align: top;
  text-decoration: none;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 1;
  }
}

.transparent-btn {
  background: transparent;
  border: none;
  padding: 0;
  margin: 0;
  white-space: nowrap;

  .dotted {
    font-size: 16px;
    display: inline-block;
    position: relative;
    white-space: nowrap;
    &::after {
      display: block;
      content: '...........................................................................................................';
      position: absolute;
      width: 100%;
      overflow: hidden;
      left: 0;
      bottom: -.45em;
      transition: color .25s;
    }
  }
  &:hover {
    text-decoration: none;
    .dotted {
      &::after {
        color: transparent;
      }
    }
  }
}

.common-btn {
  display: inline-block;
  padding: 7px 10px;
  //transition: all .25s;
  position: relative;
  background-color: @disable;
  border: 1px solid @disable;
  white-space: nowrap;
  transition: box-shadow .25s;
  line-height: 1;
  text-decoration: none;
  @media (min-width: 768px) {
    padding: 14px 20px;
  }
  .text {
    text-align: center;
    font-size: 16px;
    display: block;
    line-height: 1.4;
    pointer-events: none;
    vertical-align: middle;
    white-space: normal;
    color: white;
    text-transform: uppercase;

  }
  strong {
    font-weight: bold;
  }

  &:hover {
    text-decoration: none;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.25);
    //box-shadow: 0 0 0 0 transparent;
  }
  &:focus {
    text-decoration: none;
    box-shadow: 0 0 0 0 transparent;

  }
  &:active {
    box-shadow: 0 0 0 0 transparent;
    top: 1px;
  }
}

.btn-block {
  display: block;
}

.menu-btn {

  background: @dark_blue;
  padding: 6px;
  line-height: 3px;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  .icon {
    font-size: 30px;
    color: white;
    @media (min-width: 768px) {
      font-size: 38px;
    }
  }

}

.close-btn,
.mfp-close-btn-in .mfp-close {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 44px;
  height: 44px;
  line-height: 44px;
  font-style: normal;
  font-size: 28px;
  border-radius: 50%;
  font-family: Arial, Baskerville, monospace;
  color: @blue;
  background-color: fade(white, 75%);
  opacity: 1;
  z-index: 10;
  &:hover {
    background-color: white;
  }
}

.mfp-close-btn-in .mfp-close {
  top: 0;
  right: 0;
  @media (min-width: 768px) {
    top: 15px;
    right: 15px;
  }
}

.main-btn {
  background: @red;
  border-color: @red;
  border-radius: 10000px;
  padding-left: 50px;
  padding-right: 50px;
  @media (min-width: 768px) {
    padding-left: 50px;
    padding-right: 50px;
  }
  .icon {
    color: white;
    font-size: 44px;
    display: inline-block;
    vertical-align: middle;
    margin-right: 15px;
  }
  .text {
    display: inline-block;
    vertical-align: -.75em;
    text-transform: none;
    font-weight: bold;
    font-size: 20px;
    line-height: 1;
    text-align: left;
    &.text-center {
      text-align: center;
    }
  }



}

.main-btn-wrap {
  display: inline-block;
  background: white;
  padding: 6px 66px 6px 6px;
  border-radius: 10000px;
  max-width: 100%;
  @media (min-width: 768px) {

  }
}

.gray-rounded-btn {
  border-radius: 100px;
  .text {
    color: @black_text;
    text-transform: none;
    font-size: 18px;
  }
  @media (min-width: 768px) {
    padding: 9px 30px;
  }
}


.blue-btn {
  background: @blue;
  border-color: @blue;
  border-radius: 10000px;
  padding-left: 50px;
  padding-right: 50px;
  @media (min-width: 768px) {
    padding-left: 50px;
    padding-right: 50px;
  }
  .text {
    text-transform: none;
    font-weight: 300;
    font-size: 12px;
    color: white;
    @media (min-width: 768px) {
      font-size: 22px;
    }
  }



}
