.contacts-block {
  background: white;
  .map-wrap {
    background: #efefef;
  }
  .contacts-content {
    @media (min-width: 768px) {
      flex-direction: row-reverse;
    }
  }

  .list-wrap {
    padding-top: 30px;
  }

}

.addresses-list {
  font-size: 16px;
  @media (min-width: 768px) {
    font-size: 18px;
  }
  @media (min-width: 992px) {
    max-width: 700px;
  }
  .address {
    width: 100%;
    @media (min-width: 768px) {
      width: 60%;
    }

  }
  .schedule {
    width: 100%;
    @media (min-width: 768px) {
      width: 40%;
    }

  }
  .h3 {
    font-size: 28px;
  }
  .line-wrap {
    @media (min-width: 768px) {
      border-left: 2px solid @blue;
      padding-left: 23px;
    }
  }
}

.contacts-page {
  padding-top: 15px;
}

.view-switcher {
  display: inline-block;
  background: @gray_bg;
  border-radius: 100px;
  label {
    font-size: 16px;
    font-weight: 300;
    line-height: 1;
    padding: 10px 30px;
    border-radius: 100px;
    margin-bottom: 0;
    @media (min-width: 768px) {
      font-size: 18px;
      padding: 15px 45px;
    }
    @media (min-width: 992px) {
      font-size: 22px;
      padding: 20px 60px;
    }
  }
  input:checked + label {
    background: @blue;
    color: white;
  }
}

.hot-line-box {
  background: #03588e;
  color: white;
  .link-colored(white, none);
  padding: 30px;
  position: relative;
  font-size: 21px;
  margin-bottom: 30px;
  @media (min-width: 768px) {
    font-size: 21px;
    margin-bottom: 0;
    margin-top: -100px;
    padding: 40px 60px;
  }
  @media (min-width: 992px) {
    font-size: 30px;

  }
  @media (min-width: 1200px) {

    text-align: center;
    padding: 60px 60px 60px 170px;

  }

  a {
    font-size: 1.5em;
    font-weight: bold;
    line-height: 1;
  }

  .label {
    display: block;

    font-weight: 300;
    text-align: left;
    line-height: 1.2;
    @media (min-width: 1200px) {
      position: absolute;
      left: 40px;
      top: 50%;
      max-width: 110px;
      transform: translateY(-50%);
    }
  }

}

.hidden-addresses-list      {
  display: none;

}
