.docs-main {
  padding-top: 15px;
  padding-bottom: 50px;
  .jq-selectbox__select {
    width: 250px;
  }
  min-height: calc(~"100vh - 242px");
}

.doc-link {
  font-size: 22px;
  display: block;
  padding-left: 2.5em;
  position: relative;
  .icon {
    position: absolute;
    left: 0;
    top: .2em;
    font-size: 2em;
  }
  color: #03588e;
  text-decoration: none;

  &:hover {
    color: darken(#03588e, 5%);
    text-decoration: none;
  }
  &:focus {
    color: #03588e;
    text-decoration: none;
  }
  &:active,
  &:active:focus {
    color: darken(#03588e, 10%);
    text-decoration: none;
  }
  .text {
    border-bottom: 1px solid #03588e;
    transition: border .25s;
  }
  &:hover {
    .text {
      border-color: transparent;
    }
  }
  .icon {
    color: #03588e;
  }
  &:hover {
    .icon {
      color: darken(#03588e, 5%);
    }
  }
  &:focus {
    .icon {
      color: #03588e;
    }
  }
  &:active,
  &:active:focus {
    .icon {
      color: darken(#03588e, 10%);
    }
  }
}

.docs-list {
  .doc-link {

    margin-bottom: 30px;
  }
}
