
.main-slider-block {
  .container {
    position: relative;

  }
  margin-bottom: 25px;
}



.main-title {

  font-weight: 300;
  margin-top: 0;
  font-size: 24px;
  margin-bottom: 20px;
  @media (min-width: 768px) {
    font-size: 48px;
    margin-bottom: 40px;
  }
  strong {
    font-weight: bold;
  }
}

.three-steps-block {
  padding-top: 25px;
  padding-bottom: 25px;
  margin-bottom: -30px;

}

.steps-list {
  .flex-item {
    position: relative;
    width: 100%;
    @media (min-width: 768px) {
      width: 33.3333%;
    }

    .text {
      margin-left: 60px;
      padding: 10px 10px 10px 20px;
      border-left: 2px solid @blue;
      display: block;
      line-height: 1.2;
      font-size: 16px;

      @media (min-width: 768px) {
        position: absolute;
        font-size: 12px;
        padding: 5px 5px 5px 15px;
        bottom: 30px;
        left: 35px;
        right: 35px;
        margin-left: 0;
      }
      @media (min-width: 992px) {
        font-size: 16px;
        padding: 10px 10px 10px 30px;
        bottom: 55px;
        left: 50px;
        right: 50px;
      }
      @media (min-width: 1200px) {
        font-size: 20px;
      }
    }
    .img-wrap {
      border-radius: 5px;

    }
    @media (max-width: 767px) {
      &::before {
        left: 15px!important;
        top: 50%!important;
        transform: translateY(-50%)!important;
        margin-top: 0;
        margin-left: 0;
      }
    }
  }
  @media (min-width: 992px) {
    .flex-item {
      width: 33.333333%;

    }
  }
  li {
    padding-left: 15px !important;
    margin-bottom: 30px !important;
    min-height: 0 !important;
    padding-top: 0 !important;
    font-size: 24px;
    @media (min-width: 992px) {
      font-size: 36px;
    }
    &::before {
      position: absolute;
      margin-top: -8px;
      margin-left: 7px;

      z-index: 10;

    }

  }
}

.docs-block {
  padding-top: 25px;
  padding-bottom: 25px;
}

.blue-box {
  border-radius: 5px;
  background: @blue;
  color: white;
  padding: 30px 15px 10px;
  position: relative;
  @media (min-width: 768px) {
    padding: 30px 15px 10px;
  }
  @media (min-width: 992px) {
    padding: 50px 45px 20px;
  }
  &::after {
    display: block;
    content: "";
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 25px 16px 25px;
    border-color: transparent transparent @blue transparent;
    position: absolute;
    bottom: 100%;
    left: 195px;
  }
  .flex-item {

    margin-bottom: 20px;
    .icon {
      display: none;
    }
    @media (min-width: 768px) {
      margin-bottom: 20px;
      &:not(:last-child) {
        border-right: 1px solid white;
      }

      .icon {
        font-size: 45px;
        display: block;
        margin-bottom: 15px;
      }
    }
    @media (min-width: 992px) {
      margin-bottom: 30px;
      padding-left: 80px;
      position: relative;
      &:not(:last-child) {
        border-right: none;
      }
      .icon {
        position: absolute;
        top: 0;
        left: 0;
        font-size: 55px;
        display: block;
      }
    }

    .text {

      //padding-right: 40px;
      font-size: 14px;
      font-weight: 300;
      line-height: 1.2;
      height: 100%;
      @media (min-width: 768px) {
        font-size: 12px;
      }
      @media (min-width: 992px) {
        border-left: 3px solid white;
        padding-left: 40px;
        font-size: 14px;
      }
      .main {
        font-size: 1.285714285714286em;
        margin-bottom: 10px;
      }
    }

    @media (min-width: 768px) {

      width: 100%;

    }

    @media (min-width: 992px) {

      width: 33.333333%;

    }
  }

}

.why-block {
  padding-top: 25px;
  padding-bottom: 62px;
  margin-bottom: -37px;
  overflow: hidden;
}

.why-slider {
  margin: -62px;
  .slick-list {
    padding: 62px 47px;
  }
}

.why-list {
  margin-left: -15px;
  margin-right: -15px;
}

.why-list,
.why-slider{

  .top {
    padding: 20px 15px 20px;
    position: relative;

    @media (min-width: 768px) {
      padding: 20px 15px 20px;
      &::after {
        display: block;
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 3px;
        background: @blue;
        border-radius: 3px;
        transition: all .25s;
      }
    }
    @media (min-width: 992px) {
      padding: 30px 25px 25px;
    }
    display: flex;
    align-items: center;
    .img-wrap {
      width: 90px;
      height: 90px;
      margin-right: 15px;
      @media (min-width: 768px) {
        width: 50px;
        height: 50px;
      }
      @media (min-width: 992px) {
        width: 90px;
        height: 90px;
      }
      img {
        width: auto;
        height: 100%;
      }

    }
    .text {
      width: calc(~"100% - 105px");
      font-size: 18px;
      @media (min-width: 768px) {
        font-size: 14px;
        width: calc(~"100% - 65px");
      }
      @media (min-width: 992px) {
        font-size: 16px;
        width: calc(~"100% - 105px");
      }
      @media (min-width: 1200px) {
        font-size: 22px;
      }
    }
  }
  .bottom {
    background: @blue;
    color: white;
    font-weight: 300;
    padding: 15px 25px;
    line-height: 1.2;
    opacity: 1;
    transition: all .25s;
    @media (min-width: 768px) {
      opacity: 0;
      font-size: 12px;
      padding: 10px 15px;
    }
    @media (min-width: 992px) {
      font-size: 14px;
      padding: 15px 25px;
    }
  }
  .tile {
    border-radius: 5px;
    overflow: hidden;
    transition: all .25s;
    cursor: pointer;
    box-shadow: 0 9px 62px fade(#232323, 14%);
    @media (min-width: 768px) {
      box-shadow: 0 0 0 fade(#232323, 0);
      &:hover {
        box-shadow: 0 9px 62px fade(#232323, 14%);
        transform: scale(1.071428571428571) translateZ(0);
        .bottom {
          opacity: 1;
        }
        .top {
          &::after {
            opacity: 0;
          }
        }
      }
    }

  }
  .flex-item {
    width: 100%;
    @media (min-width: 768px) {
      width: 33.333333%;
    }

  }
  .slide {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.loans-block {
  padding-top: 25px;
  padding-bottom: 25px;
  overflow: hidden;
  p {
    font-size: 16px;
  }
}

.loans-slider {
  margin: -62px;
  position: relative;
  &::before {
    display: block;
    content: "";
    position: absolute;
    top: 62px;
    bottom: 62px;
    left: 62px;
    right: 62px;
    border-radius: 5px;
    box-shadow: 0 9px 62px fade(#232323, 14%);
    //background: @blue;
  }
  .slick-list {
    padding: 62px;


    .slick-slide {
      //margin-top: -30px;
      //margin-bottom: -30px;
      padding-left: 15px;
      padding-right: 15px;
      height: initial;
      > div {
        height: 100%;
      }
      .slide {
        height: 100%;
      }
    }
  }
  .slick-track {
    display: flex;
    align-items: stretch;

  }

}

.loans-list {
  border-radius: 5px;
  box-shadow: 0 9px 62px fade(#232323, 14%);
  margin-left: 0;
  margin-right: 0;
  width: 100%;
}

.loans-list,
.loans-slider {

  .tile {
    position: relative;
    transform-style: preserve-3d;
    transform: rotateY(0deg) translateZ(0);
    transition: all .5s;
    margin-left: -15px;
    margin-right: -15px;
    //border: 1px solid @blue;
    z-index: 0;
    height: 100%;
    border-radius: 4px;
    cursor: pointer;
    .front {
      position: relative;
      padding: 40px 30px;
      transform: rotateY(0deg) translateZ(0);
      backface-visibility: hidden;
      //background: white;
      border-radius: 4px;
      height: 100%;

      //pointer-events: none;
    }
    .back {
      //pointer-events: none;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      backface-visibility: hidden;
      transform: rotateY(180deg) translateZ(0);
      background: white;
      padding: 27px 30px 40px;
      border-top: 13px solid @blue;
      border-radius: 4px;
      .text {
        height: calc(~"100% - 2px");
        overflow: hidden;
        font-size: 14px;
        @media (min-width: 992px) {
          font-size: 12px;
        }
        @media (min-width: 1200px) {
          font-size: 14px;
        }
      }
    }

    .title {
      color: @blue;
      font-size: 22px;
      line-height: 1;
      height: 2em;
      margin-bottom: 10px;
      @media (min-width: 768px) {
        font-size: 20px;
      }
      @media (min-width: 1200px) {
        font-size: 22px;
      }

    }

    .icon {
      color: @blue;
      font-size: 60px;
      margin-left: 10px;
      margin-bottom: 35px;
    }

    .subtitle {
      color: @blue;
      font-size: 16px;
      line-height: 1;
      font-weight: bold;
      margin-bottom: 15px;
      height: 2em;


      @media (min-width: 768px) {
        font-size: 14px;
      }
      @media (min-width: 1200px) {
        font-size: 16px;
      }
    }

    .sums {
      font-size: 16px;
      margin-bottom: 15px;
      line-height: 1;
      strong {
        font-size: 1.375em;
        font-weight: bold;
      }
      @media (min-width: 768px) {
        font-size: 14px;
      }
      @media (min-width: 1200px) {
        font-size: 16px;
      }
    }
    .terms {
      font-size: 16px;
      margin-bottom: 30px;
      line-height: 1;
      strong {
        font-weight: bold;
      }
    }

    .doc {
      position: relative;
      padding-left: 45px;
      font-size: 16px;
      line-height: 1.2;
      padding-top: 7px;
      @media (min-width: 768px) {
        font-size: 12px;
        padding-left: 30px;
      }
      @media (min-width: 1200px) {
        font-size: 16px;
        padding-left: 45px;
      }
      &::before {
        display: block;
        content: "";
        position: absolute;
        width: 33px;
        height: 33px;
        border-radius: 50%;
        background: @blue;
        top: 0;
        left: 0;
        @media (min-width: 768px) {
          width: 24px;
          height: 24px;
        }
        @media (min-width: 1200px) {
          width: 33px;
          height: 33px;
        }
      }
      &::after {
        display: block;
        content: "";
        position: absolute;
        width: 14px;
        height: 10px;
        border-left: 2px solid white;
        border-bottom: 2px solid white;
        transform: rotate(-45deg);
        top: 9px;
        left: 10px;
        border-bottom-left-radius: 2px;
        @media (min-width: 768px) {
          top: 7px;
          left: 7px;
          width: 10px;
          height: 7px;
        }
        @media (min-width: 1200px) {
          top: 9px;
          left: 10px;
          width: 14px;
          height: 10px;
        }
      }
    }
    &.flip {
      transform: scale(1.071428571428571) rotateY(-180deg) translateZ(0);
      box-shadow: 0 9px 62px fade(#232323, 14%);
      z-index: 50;
    }
  }
  .slick-slide:not(.slick-current) {
    position: relative;
    &::after {
      display: block;
      content: "";
      position: absolute;
      z-index: 1;
      top: 40px;
      bottom: 40px;
      left: 0;
      border-right: 1px dashed #e3e3e3;
    }

  }
  .flex-item {
    position: relative;
    &:not(:last-child) {
      &::after {
        display: block;
        content: "";
        position: absolute;
        z-index: 1;
        top: 40px;
        bottom: 40px;
        right: 0;
        border-right: 1px dashed #e3e3e3;
      }
    }

    width: 100%;
    @media (min-width: 768px) {
      width: 33.3333333%;
    }
    @media (min-width: 992px) {
      width: 20%;
    }

  }
}

.desktop {
  .loans-list,
  .loans-slider {
    .flex-item,
    .slide {

      &:hover {
        .tile {
          transform: scale(1.071428571428571) rotateY(-180deg) translateZ(0) ;
          box-shadow: 0 9px 62px fade(#232323, 14%);
          z-index: 50;
        }
      }

    }
  }
}

.desktop.ie {
  .loans-list,
  .loans-slider {
    .flex-item,
    .slide {
      .tile {
        .doc {
          height: 64px;
        }
      }
      &:hover {
        .tile {
          background: white;
          transform: scale(1.071428571428571) translateZ(0) ;
          box-shadow: 0 9px 62px fade(#232323, 14%);
          z-index: 50;
        }
      }

    }
  }
}

.numbers-slider-block {

  background: #49bdcc;
  padding-top: 30px;
  padding-bottom: 30px;
  @media (min-width: 768px) {
    padding-top: 40px;
    padding-bottom: 50px;
    background-image: url(../img/numbers_bg.jpg);
    background-size: cover;
    background-position: center;
  }
  @media (min-width: 922px) {
    padding-top: 80px;
    padding-bottom: 95px;
  }
}

.map-block {
  background: @gray_bg;
}

.tabs-row {
  padding-top: 50px;
  padding-bottom: 50px;
  background: white;
  label {
    text-transform: uppercase;

    font-weight: 300;
    color: #b4b4b4;
    position: relative;
    font-size: 26px;
    @media (min-width: 768px) {
      font-size: 30px;
    }
    @media (min-width: 922px) {
      font-size: 40px;
    }
    @media (min-width: 1200px) {
      font-size: 48px;
    }
    &::after {
      display: block;
      content: "...............................................";
      position: absolute;
      left: 0;
      right: 0;
      top: 30%;
      overflow: hidden;
    }
    strong {
      font-weight: bold;
    }
  }
  input:checked + label {
    color: @black_text;
    &::after {
      opacity: 0;
    }
  }
}

.sity-selector-wrap {

  padding: 40px;
  background: #ef0b21;
  margin-left: -15px;
  margin-right: -15px;
  margin-top: -30px;
  @media (min-width: 768px) {
    margin-left: 0;
    margin-right: 0;
    margin-top: 0;
    position: absolute;
    transform: translateY(-25%);
    z-index: 50;
    box-shadow: 0 22px 24px fade(#ef0b21, 31%);
    border-radius: 5px;
  }
  .jq-selectbox {
    width: 100%;
  }
  .jq-selectbox__select {

    background: #ef0b21;
    border-color: #ef0b21;
    line-height: 40px;
    padding-left: 0;
    color: @black_text;
    width: 100%;
    @media (min-width: 768px) {
      width: 325px;
    }

  }

  .jq-selectbox__select-text {
    background: white;
    padding-left: 13px;
    border-radius: 20px;
    text-transform: none;
  }
  .jq-selectbox__trigger-arrow {
    color: white;

    @media (min-width: 768px) {
      margin-top: -20px;
    }
  }
  .opened .jq-selectbox__trigger-arrow {
    @media (min-width: 768px) {
      margin-top: -22px;
    }
  }
  .jq-selectbox__dropdown {
    width: calc(~"100% - 35px");
    min-width: calc(~"100% - 35px");
  }
}

.numbers-slider-block {
  color: white;
  .content-wrap {

    @media (min-width: 922px) {
      padding-left: 20px;
    }
    @media (min-width: 1200px) {
      padding-left: 70px;
    }

  }
  .number-row {
    line-height: 1;

    .money {
      font-size: 40px;
      font-weight: 300;
      display: block;
      @media (min-width: 768px) {
        font-size: 50px;
      }
      @media (min-width: 922px) {
        font-size: 64px;
      }
      @media (min-width: 1200px) {
        font-size: 72px;
      }
    }
    .number {

      font-weight: bold;
      display: block;
      font-size: 36px;

      @media (min-width: 768px) {
        font-size: 46px;
      }
      @media (min-width: 922px) {
        font-size: 56px;
      }
      @media (min-width: 1200px) {
        font-size: 60px;
      }
    }
    .text {
      position: absolute;
      bottom: 0;
      text-transform: uppercase;
      font-size: 24px;
      font-weight: 300;
      line-height: 1.1;
      strong {
        font-weight: bold;
      }
    }
    .number-item {
      position: relative;
      padding-bottom: 60px;

      min-width: 110px;
      height: 100%;
    }
  }

  .slider-track {
    background: white;
    border: none;
    box-shadow: inset 0 0 1px 1px @gray;
  }
  .slider-tick {
    background: @gray;
    box-shadow: 0 0 1px 1px @gray;
    border: 7px solid white;
    opacity: 1;
    &.in-selection {
      background: #06434b;
      border: none;
      box-shadow: none;
    }
  }

  .slider.slider-horizontal {
    width: 100%;
  }

  .slider-selection.tick-slider-selection,
  .slider-selection {
    background: #06434b;
  }

  .slider-handle {
    background: white;
    border: 7px solid #06434b;
  }

  .slider.slider-disabled .slider-track,
  .slider.slider-disabled .slider-handle {
    cursor: default;
  }
}
