html {
  .webkit-scrollbar();
  background: @gray_bg;
  &.popup-open {
    overflow-x: hidden !important;
    overflow-y: hidden !important;
    &.desktop {
      margin-right: 17px;
      &.chrome,
      &.safari {
        margin-right: 9px;
      }
    }
  }
}

body {
  max-width: 1920px;
  overflow-x: hidden;
  margin-left: auto;
  margin-right: auto;




}

section, header, footer {
  @media (min-width: 1200px) {
    transform: scale(1) translateZ(0);
  }
}

