.sticky {
  position: sticky;
}

ymaps[class*="-ground-pane"] {
  -webkit-filter: grayscale(1);
  filter: grayscale(1);
}

ymaps[class*="-balloon"] {
  box-shadow: inset 0 0 0 1px fade(@blue, 15%), 0 5px 15px -7px rgba(0, 0, 0, .5);
  padding: 0;

}

ymaps[class*="-balloon__layout"],
ymaps[class*="-balloon__content"],
ymaps[class*="-balloon_layout_panel"] {
  background: @blue;
  box-shadow: none;
}

ymaps[class*="-balloon__content"] {
  padding: 20px;
  color: white;
  font-weight: 300;
  font-size: 14px;
  font-family: @base-font;
  line-height: 1.4;
