.lined-link {
  color: @blue;
  text-decoration: none;

  &:hover {
    color: darken(@blue, 5%);
    text-decoration: none;
  }
  &:focus {
    color: @blue;
    text-decoration: none;
  }
  &:active,
  &:active:focus {
    color: darken(@blue, 10%);
    text-decoration: none;
  }
  .text {
    border-bottom: 1px solid @blue;
    transition: border .25s;
  }
  &:hover {
    .text {
      border-color: transparent;
    }
  }
}

.dotted-link {
  color: @blue;
  text-decoration: none;

  &:hover {
    color: darken(@blue, 5%);
    text-decoration: none;
  }
  &:focus {
    color: @blue;
    text-decoration: none;
  }
  &:active,
  &:active:focus {
    color: darken(@blue, 10%);
    text-decoration: none;
  }
  .text {
    border-bottom: 1px dotted @blue;
    transition: border .25s;
  }
  &:hover {
    .text {
      border-color: transparent;
    }
  }
}

.link-with-icon {
  .icon {
    color: #bbb;
    font-size: 1.375em;
    margin-right: 0.3181818181818182em;
    display: inline-block;
    vertical-align: middle;
    transition: color .25s;
  }
  &:hover {
    .icon {
      color: darken(@blue, 5%);
    }
  }
  &:focus {
    .icon {
      color: @blue;
    }
  }
  &:active,
  &:active:focus {
    .icon {
      color: darken(@blue, 10%);
    }
  }
}
