.calculator {
  .h3 {
    font-weight: 300;
    strong {
      font-weight: bold;
    }
  }
  .slider.slider-horizontal {
    width: 100%;
    .tooltip {
      transform: none;

    }
  }
  p {
    font-size: 12px;
  }
}

.calc-form {
  hr {
    border-color: @gray;
    margin-top: 10px;
    margin-bottom: 25px;
  }
  .form-group {
    position: relative;

    @media (min-width: 768px) {
      padding-left: 160px;
    }
    @media (min-width: 992px) {
      padding-left: 190px;
    }
    label {

      font-size: 16px;

      line-height: 1.2;
      @media (min-width: 768px) {
        max-width: 100px;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    &.slider-group {

      @media (min-width: 768px) {
        padding-left: 100px;
        margin-bottom: 30px;
      }
    }
    .slider {
      .tooltip.top {
        display: none;
        @media (min-width: 768px) {
          display: block;
          position: absolute;
          left: -100px!important;
          top: 50%;
          transform: translateY(-50%);
          margin-top: 0;
          font-size: 22px;
          font-weight: 300;
          color: @gray_text;
        }
      }
    }

  }
  .jq-number input,
  .form-control-div {
    height: 44px;
    line-height: 4px;
    padding: 15px 18px;
    background: @gray_bg;
    font-weight: bold;
    font-size: 28px;
    border: 5px solid @gray_bg;
    border-radius: 3px;
    box-shadow: inset 0 0 1px 1px white;
    @media (min-width: 768px) {
      height: 44px;
      padding: 0 30px;
      line-height: 34px;
    }
    &:focus,
    .focus {
      border-color: @blue;
    }
  }
  .form-control-div {
    color: @blue;
    font-size: 18px;
  }

  .well-item {
    color: @gray_text;
    font-size: 14px;
    font-weight: 300;
    @media (min-width: 768px) {
      font-size: 12px;
    }
    @media (min-width: 992px) {
      font-size: 14px;
    }
    strong {
      font-weight: bold;

    }
  }

  .well-row {
    .left {
      width: 100%
    }
    .right {
      padding-top: 30px;
      width: 100%
    }
    @media (min-width: 768px) {
      .left {
        width: 160px;
        padding-right: 0;
      }
      .right {
        width: calc(~"100% - 160px");

      }
    }
    @media (min-width: 992px) {
      .left {
        width: 190px;
        padding-right: 0;
      }
      .right {
        width: calc(~"100% - 190px");

      }
    }

  }

  .slider-track {
    background: white;
    border: none;
    box-shadow: inset 0 0 1px 1px @gray;
  }
  .slider-tick {
    background: @gray;
    box-shadow: 0 0 1px 1px @gray;
    border: 7px solid white;
    opacity: 1;
    &.in-selection {
      background: @blue;
      border: none;
      box-shadow: none;
    }
  }

  .slider-selection.tick-slider-selection,
  .slider-selection{
    background: @blue;
  }

  .slider-handle {
    background: white;
    border: 7px solid @blue;
  }


}

.calc-wrap {
  @media (min-width: 768px) {
    position: absolute;
    right: 15px;
    bottom: 0;

  }
  @media (min-width: 1200px) {
    bottom: auto;
    top: 50%;
    transform: translateY(-50%);
  }
  .calculator {
    background: white;
    border-radius: 5px;
    padding: 15px 20px 0;
    margin-left: -15px;
    margin-right: -15px;
    @media (min-width: 768px) {
      margin-left: 0;
      margin-right: 0;
      padding: 35px;
      width: 450px;
      box-shadow: 0 63px 131px fade(#111214, 24%);
    }
    @media (min-width: 922px) {
      width: 540px;
    }
    .h3 {
      margin-top: 0;
      font-size: 20px;
      margin-bottom: 25px;
      @media (min-width: 768px) {
        font-size: 26px;
      }
    }
  }
}
