.mfp-content {
  @media (max-width: 767px) {
    max-height: 100vh;
    max-width: 100vw;
    overflow: auto;
  }
}

.mfp-image-holder {
  .mfp-content {
    max-width: 85%;
    @media (max-width: 767px) {
      max-height: initial;
      max-width: 85%;
      overflow: initial;
    }
  }
}

.mfp-gallery {
  button.mfp-close {
    top: 20px;
    right: -20px;
  }

}

.popup {
  max-width: 992px;
  background-color: white;
  margin-left: auto;
  margin-right: auto;
  padding: 15px;
  max-height: 95vh;
  overflow-y: auto;
  @media (min-width: 768px) {
    padding: 30px;
  }
  fieldset {
    padding: 0;
    border: none;
    legend {
      border: none;
      font-size: 16px;
      margin-bottom: 10px;
    }
  }
  &.popup-medium {
    max-width: 810px;
  }
  &.popup-small {
    max-width: 550px;
  }
  .title {
    text-transform: uppercase;
    font-weight: bold;
    font-size: 16px;
    @media (min-width: 768px) {
      font-size: 24px;
    }
  }
  &.public-popup {
    max-width: 805px;
    border-top: 8px solid @blue;
    border-radius: 5px;
    background: @gray;
    padding: 55px 120px 45px;
    .form-control {
      background: white;
      box-shadow: none;
      &:not(:focus) {
        border-color: white;
      }
    }
    .jq-selectbox__select {
      background: white;
    }
    .jq-selectbox:not(.opened) {
      .jq-selectbox__select {
        border-color: white;
      }
    }
    .decorated-title {
      margin-left: -75px;
    }
    label {
      display: block;
      font-weight: 300;
      font-size: 16px;
      position: relative;
      padding-left: 35px;
      .jq-checkbox {
        position: absolute;
        top: -3px;
        left: 0;
      }
    }
    small {
      font-size: 16px;
      font-weight: normal;
      display: block;
      line-height: 1.2;
    }
    fieldset {
      @media (min-width: 768px) {
        position: relative;
        text-align: right;
        legend {
          display: block;
          width: initial;
          position: absolute;
          left: 0;
          top: 50%;
          transform: translateY(-50%);
        }
      }
    }
    .date-selector-wrap .jq-selectbox:not(:last-child) {
      margin-right: 5px;
      @media (min-width: 768px) {
        margin-right: 15px;
      }
    }
    .jq-selectbox__select {
      padding: 0 30px 0 10px;

      @media (min-width: 768px) {
        padding: 0 55px 0 30px;
      }

    }
    @media (max-width: 767px) {
      padding: 30px 15px 20px;
      .mfp-close {
        background: transparent;
      }

      .decorated-title {
        margin-bottom: 10px;
        &::before {
          display: none;
        }
      }

      small {
        font-size: 14px;
      }

    }
    @media (min-width: 480px) and (max-width: 767px) {
      .captcha-wrap,
      input[name='captcha'] {
        max-width: 275px;
      }
    }
  }

}
