.slider {
  .slide {
    display: block !important;
    .img-wrap {
      //display: inline-block;
    }
    ~ .slide {
      display: none !important;
    }
  }
}

.slick-slide {
  &:focus,
  a {
    outline: none !important;
  }

}

.slick-track {
  margin-left: 0;
}

.slick-arrow {
  position: absolute;
  top: 50%;
  z-index: 3;
  transform: translateY(-50%);
  background: transparent;
  border: 1px solid @blue;
  border-radius: 50%;
  transition: background .25s;
  padding: 11px;
  margin-top: -17px;
  @media (min-width: 768px) {
    padding: 11px;
  }
  span {
    display: block;
    width: 9px;
    height: 9px;
    border-top: 1px solid @blue;
    transition: border .25s;
  }

  &.slick-prev {
    right: 100%;
    margin-right: 10px;
    span {
      border-left: 1px solid @blue;
      transform: translateX(2px) rotate(-45deg);
    }

    @media screen and (min-width: 768px) {
      margin-right: 10px;
    }

    @media screen and (min-width: 1920px) {
      margin-right: -60px;
    }
  }
  &.slick-next {
    left: 100%;
    margin-left: 10px;
    span {
      border-right: 1px solid @blue;
      transform: translateX(-2px) rotate(45deg);
    }
    @media screen and (min-width: 768px) {
      margin-left: 10px;
    }

    @media screen and (min-width: 1920px) {
      margin-left: -60px;
    }
  }
  &:focus {
    outline: none !important;
  }
  &:hover {
    //background: fade(@red, 75%);
  }

}

.loans-slider {
  .slick-arrow {


    &.slick-prev {

      margin-right: -85px;


      @media screen and (min-width: 768px) {
        margin-right: -75px;
      }


    }
    &.slick-next {

      margin-left: -85px;

      @media screen and (min-width: 768px) {
        margin-left: -75px;
      }


    }
    &:focus {
      outline: none !important;
    }
    &:hover {
      //background: fade(@red, 75%);
    }

  }
}

.slick-dots {

  button {
    display: block;
    font-size: 0;
    //width: 15px;
    //height: 15px;
    //background: @dark;
    border: none;
  }
  .slick-active {
    button {
      //background: @red;
    }
  }
  li {
    display: inline-block;
    &:not(:last-of-type) {
      margin-right: 15px;
    }

  }
}

.news-page-slider {
  margin-left: -15px;
  margin-right: -15px;
  .slide {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.news-block-slider {
  position: relative;
  .slide {
    padding-bottom: 20px;
    @media (min-width: 1200px) {
      padding-bottom: 0;
    }
  }

  .slick-dots {
    position: absolute;
    bottom: 10px;
    left: 15px;
    @media (min-width: 1200px) {
      left: 30px;
    }
    z-index: 10;
  }
}


.action-slider {
  position: relative;
  .slick-dots {
    position: absolute;
    bottom: 40px;
    right: 15px;
    z-index: 10;
    @media (min-width: 480px) {
      bottom: 10px;
    }
    button {
      background: white;
      //box-shadow: 0 0 2px black;
    }
    .slick-active {
      button {
        background: @red;
      }
    }
  }
}
