/*=====================
  Базовая типография
=====================*/

@font-face {
  font-family: 'Plumb';
  src: url('../font/Plumb/Plumb-Light.eot?#iefix') format('embedded-opentype'),
  url('../font/Plumb/Plumb-Light.woff2') format('woff2'),
  url('../font/Plumb/Plumb-Light.woff') format('woff'),
  url('../font/Plumb/Plumb-Light.ttf') format('truetype'),
  url('../font/Plumb/Plumb-Light.svg#Plumb-Light') format('svg');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Plumb';
  src: url('../font/Plumb/Plumb-Medium.eot?#iefix') format('embedded-opentype'),
  url('../font/Plumb/Plumb-Medium.woff2') format('woff2'),
  url('../font/Plumb/Plumb-Medium.woff') format('woff'),
  url('../font/Plumb/Plumb-Medium.ttf') format('truetype'),
  url('../font/Plumb/Plumb-Medium.svg#Plumb-Medium') format('svg');
  font-weight: 500;
  font-style: normal;
}

