@font-face {
  font-family: 'Plumb';
  src: url('../font/Plumb/Plumb-Bold.eot?#iefix') format('embedded-opentype'),
  url('../font/Plumb/Plumb-Bold.woff2') format('woff2'),
  url('../font/Plumb/Plumb-Bold.woff') format('woff'),
  url('../font/Plumb/Plumb-Bold.ttf') format('truetype'),
