.footer {
  background: white;
  padding-top: 15px;
  padding-bottom: 15px;
  @media (min-width: 768px) {
    padding-top: 25px;
    padding-bottom: 25px;
  }
}

.popup-btn-wrap {

  @media (min-width: 768px) {
    border-left: 1px solid #dfdfdf;
    border-right: 1px solid #dfdfdf;
    padding-top: 20px;
    padding-bottom: 20px;
  }
  button {
    color: @blue;
    margin-left: 15px;
    margin-right: 15px;
    font-size: 14px;
    .icon {
      font-size: 2.571428571428571em;
      margin-right: 1em;
      vertical-align: middle;
    }
    .text {
      text-transform: uppercase;
      font-weight: bold;
      border-bottom: 6px solid currentColor;
      display: inline-block;
      vertical-align: middle;
      line-height: 1;
      padding-top: 10px;
      padding-bottom: 10px;
    }

  }
}



