.breadcrumbs {
  color: @black_text;
  .link-colored(@black_text, none);
  a {
    .text {
      border-bottom: 1px solid fade(@black_text, 50%);
    }
    &:hover {
      .text {
        border-color: transparent;
      }
    }
  }
  li {
    margin-bottom: 5px;
  }
  li:not(:last-of-type) {
    position: relative;
    &::after {
      display: inline;
      content: '/';
      position: absolute;
      right: -5px;
      top: -2px;
      font-size: 1.2em;
      font-weight: 300;
    }
  }
}
